const colors = [
  '#FFFFFF',
  '#2196F3',
  '#4CAF50',
  '#FF9800',
  '#009688',
  '#795548',
];

const randomIntegerFromInterval = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};

const refs = {
  body: document.querySelector('BODY'),
  startBtn: document.querySelector('[data-action = "start"]'),
  stopBtn: document.querySelector('[data-action = "stop"]'),
};

let intervalId = null;

refs.startBtn.addEventListener('click', onStartClickHandler);
refs.stopBtn.addEventListener('click', onStopClickHandler);

function onStartClickHandler() {
  intervalId = setInterval(randomChangeBodyColor, 1000);
  refs.startBtn.removeEventListener('click', onStartClickHandler);
}

function onStopClickHandler() {
  clearInterval(intervalId);
  refs.startBtn.addEventListener('click', onStartClickHandler);
}

function randomChangeBodyColor() {
  refs.body.style.backgroundColor =
    colors[randomIntegerFromInterval(0, colors.length - 1)];
}
